<template>
  <v-navigation-drawer
    permanent
    v-model="drawer"
    :mini-variant.sync="mini"
    class="float-left side-bar"
    id="drawer-menu"
    dark
  >
    <div>
      <v-list-item class="px-3 pt-3 pb-3">
        <v-list-item-avatar style="padding: 3px">
          <v-img :src="logo"></v-img>
        </v-list-item-avatar>
        <v-list-item-title class="text-left">
          <span>Rubix Platform</span>
          <p class="text-center text-xs caption mb-n2 orange--text" v-if="isSwitchedSlave">
            Slave Mode Active
            <tooltip-icon-button
              :onClick="exitSlaveMode"
              btnIcon="mdi-location-exit"
              btnTooltip="Exit slave mode"
              btn-color="orange"
            ></tooltip-icon-button>
          </p>
          <div class="switch-device-title" v-if="deviceInfo">
            <v-tooltip bottom color="black">
              <template v-slot:activator="{ on, attrs }">
                <span class="client-name" v-bind="attrs" v-on="on"> {{ deviceInfo.client_name }}</span>
              </template>
              <span>
                <sidebar-device-info
                  v-if="deviceInfo"
                  :clientName="deviceInfo.client_name"
                  :siteName="deviceInfo.site_name"
                  :deviceName="deviceInfo.device_name"
                  title="Current Device"
                >
                </sidebar-device-info>
                <sidebar-device-info
                  v-if="isSwitchedSlave && switchSlaveDeviceInfo"
                  :clientName="switchSlaveDeviceInfo.client_name"
                  :siteName="switchSlaveDeviceInfo.site_name"
                  :deviceName="switchSlaveDeviceInfo.device_name"
                  title="Switched Device"
                >
                </sidebar-device-info>
              </span>
            </v-tooltip>
          </div>
        </v-list-item-title>
        <v-btn icon @click.stop="minimizeMenu">
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
      </v-list-item>
    </div>

    <div class="text-left pl-4 lock-icon-button">
      <tooltip-icon-button
        :btnColor="setting.lockSidebar ? 'blue' : ''"
        buttonSize="small"
        btnIcon="mdi-lock"
        btnTooltip="Lock/Unlock Sidebar"
        :onClick="() => changeLock()"
      ></tooltip-icon-button>
    </div>
    <v-divider></v-divider>
    <v-text-field
      v-if="!mini && sidebarSearch"
      class="pa-3 pt-5"
      v-model="search"
      append-icon="mdi-magnify"
      label="Search"
      dense
      single-line
      hide-details
    ></v-text-field>
    <vertical-height-container :vh="sidebarContainerHeight">
      <template v-slot:children>
        <div class="list-box-2">
          <v-list dense nav :key="renderCount">
            <v-list-group
              class="text-left grey--text"
              v-for="(sidebarItemGroup, key) in groupedSidebarItems"
              :key="key"
              :sub-group="true"
              :value="showAllMenus"
              prepend-icon=""
            >
              <template v-slot:activator>
                <v-list-item-title :class="`sidebar-group-item ${mini ? 'pl-1' : 'pl-3'}`">
                  <span v-if="!mini">{{ sidebarItemGroup.group.displayName }}</span>
                  <tooltip-title v-if="mini" :title="sidebarItemGroup.group.displayName"></tooltip-title>
                  <v-icon v-if="!mini">mdi-chevron-down</v-icon>
                </v-list-item-title>
              </template>
              <div v-for="sidebarItemKey in sidebarItemGroup.keys" :key="sidebarItemKey">
                <v-list-item
                  v-if="sidebarItemsMapping[sidebarItemKey] && sidebarItemsMapping[sidebarItemKey].type == 'item'"
                  link
                  :to="sidebarItemsMapping[sidebarItemKey].link"
                >
                  <v-list-item-icon>
                    <v-tooltip right>
                      <template v-slot:activator="{ on, attrs }">
                        <v-icon v-bind="attrs" v-on="on"> {{ sidebarItemsMapping[sidebarItemKey].icon }} </v-icon>
                      </template>
                      <span>{{ sidebarItemsMapping[sidebarItemKey].title }}</span>
                    </v-tooltip>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>{{ sidebarItemsMapping[sidebarItemKey].title }}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </div>
            </v-list-group>
            <v-list-item v-bind:key="iframe._id" v-for="iframe in iframes" link>
              <v-list-item-icon @click="onClickIframeItem(iframe)">
                <v-tooltip right>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on"> {{ `mdi-${iframe.icon}` }} </v-icon>
                  </template>
                  <span>{{ iframe.name }}</span>
                </v-tooltip>
              </v-list-item-icon>
              <v-list-item-content @click="onClickIframeItem(iframe)">
                <v-list-item-title style="text-align: start">{{ iframe.name }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-action v-if="iframe.type !== 'iframeOnly'">
                <v-icon @click="clickOnLinkIcon(iframe)" small color="grey lighten-1">mdi-open-in-new</v-icon>
              </v-list-item-action>
            </v-list-item>
            <v-divider></v-divider>
            <v-list-item link :to="'/setting'">
              <v-list-item-icon>
                <v-tooltip right>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on"> mdi-settings </v-icon>
                  </template>
                  <span>Settings</span>
                </v-tooltip>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title style="text-align: start">Settings</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </div>
      </template>
    </vertical-height-container>

    <div class="list-box-3">
      <div>
        <div v-if="!mini">
          <div class="mini-device-info white--text" v-if="deviceInfo">
            <p class="overline">Current Device</p>
            <p><span class="info-title nubeYellow--text">Site Name: </span>{{ deviceInfo.site_name }}</p>
            <p><span class="info-title nubeYellow--text">Device Name: </span>{{ deviceInfo.device_name }}</p>
          </div>
          <div class="mini-device-info white--text" v-if="isSwitchedSlave && switchSlaveDeviceInfo">
            <p class="overline">Switched Device</p>
            <p><span class="info-title nubeYellow--text">Site Name: </span>{{ switchSlaveDeviceInfo.site_name }}</p>
            <p><span class="info-title nubeYellow--text">Device Name: </span>{{ switchSlaveDeviceInfo.device_name }}</p>
          </div>
        </div>
        <v-list-item v-if="mini">
          <v-tooltip bottom color="black">
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-bind="attrs" v-on="on" medium>mdi-information-outline</v-icon>
            </template>
            <span>
              <sidebar-device-info
                v-if="deviceInfo"
                :clientName="deviceInfo.client_name"
                :siteName="deviceInfo.site_name"
                :deviceName="deviceInfo.device_name"
                title="Current Device"
              >
              </sidebar-device-info>
              <sidebar-device-info
                v-if="isSwitchedSlave && switchSlaveDeviceInfo"
                :clientName="switchSlaveDeviceInfo.client_name"
                :siteName="switchSlaveDeviceInfo.site_name"
                :deviceName="switchSlaveDeviceInfo.device_name"
                title="Switched Device"
              >
              </sidebar-device-info>
            </span>
          </v-tooltip>
        </v-list-item>
      </div>
      <nav-bar-three-dot-settings> </nav-bar-three-dot-settings>
    </div>
  </v-navigation-drawer>
</template>

<script>
import _cloneDeep from 'lodash/cloneDeep';
import logo from '../../assets/nube-frog-green.png';
import UpdatePasswordForm from './UpdatePasswordForm';
import SidebarDeviceInfo from './SidebarDeviceInfo.vue';
import TooltipTitle from '../RubixApps/TooltipTitle.vue';
import { sidebarItems } from '../../constants/appConstant';
import NavBarThreeDotSettings from './NavBarThreeDotSettings';
import * as rubixService from '../../services/rubixService';
import * as settingService from '../../services/settingService';
import * as slaveAppsUiService from '../../services/slaveAppsUiService';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import VerticalHeightContainer from '../Common/VerticalHeightContainer.vue';

export default {
  components: {
    TooltipTitle,
    UpdatePasswordForm,
    NavBarThreeDotSettings,
    SidebarDeviceInfo,
    TooltipIconButton,
    VerticalHeightContainer,
  },
  props: ['drawer'],
  data: () => ({
    renderCount: 0,
    sidebarGroupValues: {},
    mini: true,
    switchSlaveUUID: '',
    switchSlaveDeviceInfo: null,
    logo,
    sidebarItems,
    password: {},
    passData: '',
    dialog: false,
    css: '',
    test: false,
    deviceInfo: {},
    search: '',
  }),
  computed: {
    sidebarSearch() {
      return this.$store.state.setting.sidebarSearch;
    },
    showAllMenus() {
      return this.$store.state.setting.showAllMenus;
    },
    iframes() {
      const { iframes } = this.$store.state;
      return iframes.filter(i => i.show);
    },
    isSwitchedSlave() {
      return this.$store.state.isSwitchedSlave;
    },
    items() {
      const { setting, mqttRestBridgeConfig } = this.$store.state;
      const menuItems = _cloneDeep(sidebarItems);
      const result = [];
      for (const item of menuItems) {
        if (item.hasOwnProperty('dependentKey')) {
          if (setting[item.key] && setting[item.dependentKey]) {
            result.push(item);
          }
        } else {
          if (setting[item.key]) {
            result.push(item);
          }
        }

        if (item.key == 'masterSlaves' && mqttRestBridgeConfig && mqttRestBridgeConfig.master) {
          result.push(item);
        } else if (item.type === 'divider' && item.keys.map(key => setting[key]).some(val => val)) {
          result.push(item);
          if (item.hasOwnProperty('dependentKey') && !setting[item.dependentKey]) {
            result.pop();
          }
        }
      }
      return result.filter(val =>
        this.search && val && val.type === 'item'
          ? val.title.toLowerCase().indexOf(this.search.toLowerCase()) !== -1
          : true,
      );
    },
    groupedSidebarItems() {
      return this.items.reduce((acc, curr) => {
        if (curr.type === 'divider') {
          acc[curr.group.name] = curr;
        }
        return acc;
      }, {});
    },
    sidebarItemsMapping() {
      return this.items.reduce((acc, curr) => {
        if (curr.type === 'item') {
          acc[curr.key] = curr;
        }
        return acc;
      }, {});
    },
    setting() {
      return this.$store.state.setting;
    },
    showSidebarContent() {
      return this.mini && this.deviceInfo;
    },
    sidebarContainerHeight() {
      let containerHeight = 86;
      if (this.mini) {
        containerHeight = containerHeight - 6;
      } else {
        if (this.deviceInfo) {
          containerHeight = containerHeight - 7;
        }
        if (this.isSwitchedSlave && this.deviceInfo) {
          containerHeight = containerHeight - 7;
        }
        if (this.sidebarSearch) {
          containerHeight = containerHeight - 7;
        }
      }

      return containerHeight;
    },
  },
  watch: {
    isSwitchedSlave(newVal, oldVal) {
      const selectedDeviceInfo = slaveAppsUiService.getAppSlaveDeviceInfo();
      if (selectedDeviceInfo) {
        this.switchSlaveDeviceInfo = selectedDeviceInfo;
      }
    },
  },
  mounted() {
    this.mini = !this.setting.lockSidebar;
    this.getDeviceInfo();
    this.sidebarGroupValues = Object.keys(this.groupedSidebarItems).reduce((acc, key, index) => {
      if (index == 0) {
        acc[key] = true;
      }
      acc[key] = false;
      return acc;
    }, {});
    this.renderCount++;
  },
  updated() {
    this.css =
      !this.setting.lockSidebar && this.mini
        ? 'position:absolute;bottom:50px'
        : 'position:absolute;bottom:13px;right:10px';
  },
  methods: {
    onClickIframeItem({ ipType, port, type, ip, _id }) {
      let url = `http://${ip}:${port}`;
      if (ipType === 'userBrowserIp') {
        url = `http://${location.hostname}:${port}`;
      }
      if (type === 'linkOnly') {
        window.open(url, '_blank');
        return;
      }
      this.$router.replace(`/iframe/${_id}`);
    },
    clickOnLinkIcon({ ip, port, ipType }) {
      let url = 'http://' + ip + ':' + port;
      if (ipType === 'userBrowserIp') {
        url = `http://${location.hostname}:${port}`;
      }
      window.open(url, '_blank');
    },
    onCancel() {
      this.dialog = false;
      this.test = false;
    },
    changeLock() {
      this.setting.lockSidebar = !this.setting.lockSidebar;
      settingService.update(this.setting).then(result => {
        if (result === 'successful') {
          this.$store.commit('changeSetting', this.setting);

          this.$toasted.show('Success!');
        }
      });
    },
    minimizeMenu() {
      if (!this.setting.lockSidebar) {
        this.mini = !this.mini;
      }
    },
    getDeviceInfo() {
      rubixService.fetchPlatInfo().then(data => {
        this.$store.commit('changeSetting', data);
        this.deviceInfo = data;
      });
    },
    exitSlaveMode() {
      slaveAppsUiService.removeAppSlaveId();
      window.location.reload();
      this.$store.commit('changeAppRerenderCount');
    },
  },
};
</script>
