<template>
  <span class="pl-3">
    <v-tooltip v-if="isConnected" bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-icon v-bind="attrs" v-on="on" color="light-green">mdi-pipe</v-icon>
      </template>
      <span>MQTT rest bridge connected</span>
    </v-tooltip>

    <v-tooltip v-if="!isConnected" bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-icon v-bind="attrs" v-on="on" color="red">mdi-pipe-disconnected</v-icon>
      </template>
      <span>MQTT rest bridge disconnected</span>
    </v-tooltip>
  </span>
</template>
<script>
import * as rubixService from '../../services/rubixService';

const OFFLINE_LISTENER = 'offline';
const ONLINE_LISTENER = 'online';

export default {
  name: 'mqtt-rest-bridge-connectivity',
  data() {
    return {
      isConnected: false,
    };
  },
  mounted() {
    rubixService.pingSystemService().then(value => {
      this.isConnected = (value && value.mqtt_rest_bridge_listener && value.mqtt_rest_bridge_listener.status) || false;
    });
  },
};
</script>
