<template>
  <span>
    <v-icon v-if="isConnected" color="light-green">mdi-cloud-check-outline</v-icon>
    <v-icon v-if="!isConnected" color="red">mdi-cloud-off-outline</v-icon>
  </span>
</template>
<script>
const OFFLINE_LISTENER = 'offline';
const ONLINE_LISTENER = 'online';
export default {
  name: 'internet-connectivity',
  data() {
    return {
      isConnected: false,
    };
  },
  mounted() {
    this.initialize();
    window.addEventListener(ONLINE_LISTENER, this.onlineHandler);
    window.addEventListener(OFFLINE_LISTENER, this.offlineHandler);
  },
  methods: {
    initialize() {
      this.isConnected = window.navigator.onLine;
    },
    onlineHandler() {
      this.isConnected = true;
    },
    offlineHandler() {
      this.isConnected = false;
    },
  },
};
</script>
