<template>
  <v-app :key="rerenderCount">
    <Sidebar :drawer="drawer" />
    <v-main style="padding: 0">
      <v-container fluid>
        <v-banner single-line v-if="isServiceConfigured" class="text-left">
          <v-icon slot="icon" color="warning" size="36"> mdi-alert-circle-outline </v-icon>
          Please put credential details for BIOS service and Rubix service login
          <template v-slot:actions>
            <v-btn color="primary" text @click="showConfigureEnpoint"> Configure </v-btn>
          </template>
        </v-banner>
        <v-dialog fullscreen v-model="configureEndpointDialog">
          <configure-endpoint-view
            v-if="configureEndpointDialog"
            :onCancel="closeConfigureEnpoint"
          ></configure-endpoint-view>
        </v-dialog>
        <router-view></router-view>
      </v-container>
    </v-main>
    <v-overlay :value="loading"></v-overlay>
    <v-dialog v-model="loading" hide-overlay persistent width="300">
      <v-card color="primary">
        <v-card-text>
          Loading
          <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import Sidebar from './Sidebar/Sidebar';
import logo from '../assets/nube-frog.png';
import axios from '../axios';
import ConfigureEndpointView from './Sidebar/ConfigureEndpointView';
import * as appsService from '../services/appsService';
import * as rubixService from '../services/rubixService';
import * as actionTypes from '../store/actionTypes';
import * as rfPluginService from '../services/pluginService';
import { SERVICE_NAMES } from '../constants/appConstant.js';
import * as systemLoginService from '../services/systemLoginService';
import * as mqttRestBridgeService from '../services/mqttRestBridgeService';
import * as slaveAppsUiService from '../services/slaveAppsUiService';
import { rfCommandGroupService } from '../services/rfCommandGroupService';
import { rfStreamService } from '../services/rfStreamsService';
import { rfFlowNetworkService } from '../services/rfFlowNetworkService';
import { rfNetworkService } from '../services/rfNetworkService';
import { rfDeviceService } from '../services/rfDeviceService';
import { rfPointService } from '../services/rfPointService';
import * as rfPingService from '../services/rfPingService';

const retryDuration = 1000;
export default {
  components: { Sidebar, ConfigureEndpointView },
  data() {
    return {
      configureEndpointDialog: false,
      user: '',
      logo: logo,
      drawer: true,
      retryCount: 0,
      retryInterval: null,
      maxRetryCount: 3,
    };
  },
  computed: {
    loading() {
      return this.$store.state.loading;
    },
    isServiceConfigured() {
      this.$store.state.isServicesConfigured;
      return !this.$store.state.isServicesConfigured.rubixService || !this.$store.state.isServicesConfigured.rubixBios;
    },
    rerenderCount() {
      this.$store.state.rerenderCount;
    },
  },
  watch: {
    isServiceConfigured: function (newVal, prevVal) {
      if (prevVal !== newVal && this.retryCount < this.maxRetryCount) {
        this.startTestConnectionInterval();
      }
    },
  },
  destroyed() {
    clearInterval(this.retryInterval);
  },
  methods: {
    getPingData() {
      axios.get('point-server/ping').then(res => {
        this.$store.commit('changePingData', res);
      });
    },
    startTestConnectionInterval() {
      if (this.retryInterval) {
        return;
      }

      this.retryInterval = setInterval(() => this.incrementTestConnection(), retryDuration);
    },
    incrementTestConnection() {
      this.retryCount++;
      if (this.retryCount > this.maxRetryCount) {
        clearInterval(this.retryInterval);
        return;
      }
      [SERVICE_NAMES.RUBIX_BIOS, SERVICE_NAMES.RUBIX_SERVICE].map(serviceName => this.testConnection(serviceName));
    },
    testConnection(serviceName) {
      systemLoginService
        .testSystemIntegrationConnection(serviceName)
        .then(data => {
          this.$store.commit('changeServicesConfigured', { [serviceName]: true });
        })
        .catch(err => {
          this.$store.commit('changeServicesConfigured', { [serviceName]: false });
        });
    },
    getIframes() {
      axios.get('iframe').then(r => {
        this.$store.commit('changeIframes', r);
      });
    },
    getInstalledServices() {
      appsService.fetchAllInstalledApps().then(r => {
        this.$store.commit('changeInstalledServices', r);
      });
    },
    getMqttConfig() {
      axios.get('mqtt/config').then(r => {
        this.$store.commit('changeMqttConfig', r);
        // this.mqttConfig.subscriptions.forEach(s=>s.enable = true);
      });
    },
    getAppDetails() {
      appsService.fetchApps().then(r => {
        this.$store.commit('changeAppDetails', r);
      });
    },
    getSystemDefaultApps() {
      appsService.fetchSystemDefaultApps().then(r => {
        this.$store.commit(actionTypes.UPDATE_SYSTEM_DEFAULT_APPS, r);
      });
    },
    getRubxiServiceVersion() {
      rubixService.fetchStatus().then(r => {
        this.$store.commit(actionTypes.UPDATE_RUBIX_SERVICE_VERSION, r);
      });
    },
    pingRFServices() {
      rfPingService
        .fetch()
        .then(val => {
          if (val && val.health && val.health.toLowerCase() === 'green') {
            this.$store.commit('changeRFAvailable', true);
          } else {
            this.$store.commit('changeRFAvailable', false);
          }
        })
        .catch(err => {
          this.$store.commit('changeRFAvailable', false);
        })
        .finally(() => {
          this.initiateRF();
        });
    },
    pingBiosServices() {
      systemLoginService
        .testSystemIntegrationConnection(SERVICE_NAMES.RUBIX_BIOS)
        .then(r => {
          this.$store.commit('changeServicesConfigured', { [SERVICE_NAMES.RUBIX_BIOS]: true });
        })
        .catch(err => {
          if (err && err.statusCode === '401') {
            this.$store.commit('changeServicesConfigured', { [SERVICE_NAMES.RUBIX_BIOS]: false });
          }
        });
    },
    pingRubixServices() {
      systemLoginService
        .testSystemIntegrationConnection(SERVICE_NAMES.RUBIX_SERVICE)
        .then(r => {
          this.$store.commit('changeServicesConfigured', { [SERVICE_NAMES.RUBIX_SERVICE]: true });
        })
        .catch(err => {
          if (err && err.statusCode === '401') {
            this.$store.commit('changeServicesConfigured', { [SERVICE_NAMES.RUBIX_SERVICE]: false });
          }
        });
    },
    fetchMqttRestBridgeConfig() {
      mqttRestBridgeService.fetchMRBListener().then(data => {
        this.$store.commit('changeMqttRestBridgeConfig', data);
      });
    },
    showConfigureEnpoint() {
      this.configureEndpointDialog = true;
    },
    closeConfigureEnpoint() {
      this.configureEndpointDialog = false;
    },
    getSwitchSlaveState() {
      const slaveId = slaveAppsUiService.getAppSlaveId();
      if (!slaveId) {
        this.$store.commit('changeSwitchedSlaveState', false);
      } else {
        this.$store.commit('changeSwitchedSlaveState', true);
      }
    },
    getRfPlugins() {
      rfPluginService.fetchNetworksWithPlugins().then(value => {
        this.$store.commit('changeRfPlugins', value);
      });
    },
    getRfNetworks() {
      rfNetworkService.fetch().then(value => {
        this.$store.commit('changeRfNetworks', value);
      });
    },
    getRfDevices() {
      rfDeviceService.fetchAll().then(value => {
        this.$store.commit('changeRfDevices', value);
      });
    },
    getRfPoints() {
      rfPointService.fetchAll().then(value => {
        this.$store.commit('changeRfPoints', value);
      });
    },
    getRfCommandGroups() {
      rfCommandGroupService.fetch().then(value => {
        this.$store.commit('changeRfCommandGroups', value);
      });
    },
    getRfStreams() {
      rfStreamService.fetch().then(value => {
        this.$store.commit('changeRfStreams', value);
      });
    },
    getFlowNetworks() {
      rfFlowNetworkService.fetch().then(value => {
        this.$store.commit('changeRfFlowNetworks', value);
      });
    },
    initiateRF() {
      if (this.$store.state.setting && this.$store.state.setting.rubixFlow && this.$store.state.rfAvailable) {
        this.getRfStreams();
        this.getFlowNetworks();
        this.getRfNetworks();
        this.getRfDevices();
        this.getRfPoints();
        this.getRfPlugins();
      }
    },
  },
  created() {
    window.EventBus.$on('show-toast', $event => {
      this.$toasted.show($event, { type: 'error' });
    });
    window.EventBus.$on('change-loading', $event => {
      this.$store.commit('changeLoading', $event);
    });
    window.EventBus.$on('show-reconfigure-services', $event => {
      this.$store.commit('changeServicesConfigured', $event);
    });
  },
  mounted() {
    this.getIframes();
    this.getAppDetails();
    this.getMqttConfig();
    this.pingBiosServices();
    this.pingRubixServices();
    this.fetchMqttRestBridgeConfig();
    this.getInstalledServices();
    this.getSystemDefaultApps();
    this.getRubxiServiceVersion();
    this.getSwitchSlaveState();
    this.pingRFServices();
  },
};
</script>
<style>
.v-toolbar {
  flex: none;
}
.v-application--wrap {
  display: inline-block !important;
}
</style>
